.landing-page {
    height: 100vh;
    width: 100%;
    color: #ffffff;


    background-image: url('../../img/bg_img_web.jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    background-size: cover;

    .color-overlay {
        height: 100vh;
        width: 100%;
        position: absolute;
        background-color: rgba($color: #db0000, $alpha: 0.6);
    }

    .landing-content {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;


        .landing-logo {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            font-size: 1.5em;
            padding-top: 3em;
            height: 50%;
            width: 100%;
            
            div {
                font-family: 'Inter', sans-serif;
                text-align: center;
            }
            .logo-title,  {
                font-size: 2em;
                margin-top: 0.1em;
            }
        }


    }


}

.login-form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 50%;
    width: 20%;
    min-width: 300px;

    @media only screen and (max-width: 330px) {
        min-width: 230px;
    }
    

    .login-input, .login-btn {
        width: 100%;
        font-size: 1em;
        padding: 10px;
        border-radius: 20px;
        margin-bottom: 0.5em;
        border-style: none;
    }
    
    .login-input {
        &:focus {
            outline: none;
            background-color: #fff495;
        }
    }


    .login-btn {
        border-style: none;
        background-color: #db0000;
        color: #ffffff;
        margin: 1em;
        cursor: pointer;

        &:focus {
            outline: none;
            background-color: #be0000;
        }

        &:hover {
            background-color: #be0000;
        }
    }

    .alert {
        height: 25px;
    }

    .hide-message {
        visibility: hidden;
    }

    .forgot-password {
        margin-bottom: 0.5em;
        a {
            color: #ffffff;
        }
    }

    .no-account {
         color: #db3535;
        .sign-link {
            color: #ffffff;
        }
    }

}