$navbar-height: 7vh;
$links-height: calc(100vh - #{$navbar-height});

.center-content {

    justify-content: center;


    .login-form {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 50%;
        width: 20%;
        min-width: 300px;
        background-color: #e5e5e5;
        padding: 2em 2em;
        box-shadow: 0px 5px 10px -3px rgba(0, 0, 0, 0.3);

        @media only screen and (max-width: 310px) {
            min-width: 260px;
            padding: 2em 1em;
        }

        h2 {
            margin-bottom: 0.5em;
        }
        
        .email-label {
            align-self: flex-start;
            margin-bottom: 0.5em;
        }

        .password-field {

            margin: 1em 0;
            
            label {
                display: block;
                margin-bottom: 0.5em;
            }

            .password-guideline {
                font-size: 0.75em;
                text-align: center;
            }

        }
        
    
        .login-input, .login-btn {
            width: 100%;
            font-size: 1em;
            padding: 10px;
            border-radius: 20px;
            margin-bottom: 0.5em;
            border-style: none;
        }    

        .login-input {
            &:focus {
                outline: none;
                background-color: #fff495;
            }
        }
    
        .login-btn {
            border-style: none;
            background-color: #db0000;
            color: #ffffff;
            margin: 1em;
            cursor: pointer;

            &:focus {
                outline: none;
                background-color: #900000;
            }
        }
    
        .update_alert {
            color: #db0000;
            height: 25px;
        }

        .forgot-password {
            margin-bottom: 0.5em;
            a {
                color: #ffffff;
            }
        }
    
        .no-account {
             color: #db3535;
            .sign-link {
                color: #000000;
                cursor: pointer;
            }
        }

        .hidden {
            visibility: hidden;
        }
    
    
    }
}
